
import { defineComponent } from 'vue';
import { MatomoDialog } from 'CoreHome';

export default defineComponent({
  components: {
    MatomoDialog,
  },
  data() {
    return {
      count: 12,
      showDialog: false,
    };
  },
  setup() {
    return {
      increment() {
        this.count += 1;
        this.showDialog = this.count > 15;
      },
      decrement() {
        this.count -= 1;
        this.showDialog = this.count > 15;
      },
    };
  },
});
